<template>
  <div class="mt-4">
      <v-card-title>
      <div class="text-h5 font-weight-light">
        Report Amortisasi
      </div>
    </v-card-title>
    <v-card class="ma-2">
      <v-card-title>
        <v-autocomplete
          :loading="state.loading"
          v-model="AgreementNo"
          :items="state.users"
          label="Name / Agreement No / NIK"
          :item-text="item => item.Name + ' | ' +item.AgreementNo + ' | ' + item.NIK"
          item-value="AgreementNo"
          outlined
          @change="getData({AgreementNo:AgreementNo})"
        >
      </v-autocomplete>
      </v-card-title>
    </v-card>
    <v-card class="pa-2 ma-2">
      <v-data-table
        :headers="headers"
        :items="state.data.list"
        loading-text="Loading... Please wait"
        :loading="state.loading"
        :hide-default-footer="true"
        disable-pagination
        class="elevation-1"
      >
        <template #[`item.index`]="{ item }">
          {{ state.data.list.indexOf(item) }}
        </template>
        <template #[`item.InstallmentDueDate`]="{ item }">
            {{ toDate(item.InstallmentDueDate ? item.InstallmentDueDate : '')}}
        </template>
        <template #[`item.PrincipalAmount`]="{ item }">
            {{ item.PrincipalAmount > 0 ? toRupiah(item.PrincipalAmount) : '0'}}
        </template>
        <template #[`item.InterestAmount`]="{ item }">
            {{ item.InterestAmount > 0 ? toRupiah(item.InterestAmount) : '0'}}
        </template>
        <template #[`item.InstallmentAmount`]="{ item }">
            {{ item.InstallmentAmount > 0 ? toRupiah(item.InstallmentAmount) : '0'}}
        </template>
        <template #[`item.OSPrincipalAmount`]="{ item }">
            {{ item.OSPrincipalAmount > 0 ? toRupiah(item.OSPrincipalAmount) : '0'}}
        </template>
        <template #[`item.PaidDate`]="{ item }">
          <v-chip
            class="ma-2"
            small
            :color="item.PaidDate === null ? 'red' : 'success'"
            text-color="white"
          >
            {{ item.PaidDate === null ? 'UNPAID' : 'PAID' }}
          </v-chip>
        </template>
        <template slot="body.append">
          <tr v-if="state.data.length !== 0" class="black--text">
            <th>Totals</th>
            <th></th>
            <th class="text-right">{{ toRupiah(sumField('PrincipalAmount')) }}</th>
            <th class="text-right">{{ toRupiah(sumField('InterestAmount')) }}</th>
            <th class="text-right">{{ toRupiah(sumField('InstallmentAmount')) }}</th>
            <th class="text-right">{{ toRupiah(sumField('OSPrincipalAmount')) }}</th>
            <th></th>
          </tr>
        </template>
      </v-data-table>
    </v-card>
    <!-- <Detail/> -->
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Utils from '@/utils/util'
// import Detail from '@/components/Amortisasi/Detail.vue'
export default {
  name: 'AmortisasiView',
  components: {
    // Detail
  },
  data: () => ({
    AgreementNo: '',
    headers: [
      {
        align: 'center',
        sortable: false,
        text: 'Bulan',
        value: 'index'
      },
      {
        text: 'Due Date',
        align: 'start',
        sortable: false,
        value: 'InstallmentDueDate'
      },
      { text: 'Angsuran Pokok', value: 'PrincipalAmount', align: 'right', sortable: false },
      { text: 'Angsuran Bunga', value: 'InterestAmount', align: 'right', sortable: false },
      { text: 'Total Angsuran', value: 'InstallmentAmount', align: 'right', sortable: false },
      { text: 'Saldo Pinjaman', value: 'OSPrincipalAmount', align: 'right', sortable: false },
      { text: 'Status', value: 'PaidDate', align: 'center', sortable: false }
    ]
  }),
  mounted: function () {
    store.dispatch('amortisasi/reqDataUser')
    store.dispatch('amortisasi/setNullData')
  },
  computed: {
    ...mapState('amortisasi', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('amortisasi', [
      'getData'
    ]),
    sumField (key) {
      return this.state.data.list.reduce((a, b) => a + (b[key] || 0), 0)
    },
    toRupiah (nominal) {
      return Utils.toRupiah(nominal)
    },
    toDate (tanggal) {
      return Utils.toDateStringMount(tanggal)
    }
  }
}
</script>
